.delete-device-container {
    padding: 20px;
    background-color: #f9f9f9;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  h2 {
    color: #333;
    margin-bottom: 20px;
  }
  
  input {
    padding: 10px;
    width: 300px;
    margin-bottom: 15px;
    border-radius: 5px;
    border: 1px solid #ccc;
  }
  
  button {
    padding: 12px 20px;
    background-color: #e74c3c;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    width: 150px;
  }
  
  button:disabled {
    background-color: #bbb;
    cursor: not-allowed;
  }
  
  button:hover {
    background-color: #c0392b;
  }
  
  p {
    color: #e74c3c;
    font-weight: bold;
    margin-top: 20px;
  }
  